import { clear } from '@testing-library/user-event/dist/clear';
import { useState, useEffect } from 'react';
import {useHistory, useParams} from 'react-router-dom';
import { Nav, } from 'react-bootstrap';
import styled from 'styled-components';
import '../Detail.scss';
import { CSSTransition } from "react-transition-group";
import { Popover } from 'bootstrap';
import { connect } from 'react-redux';

let 박스 = styled.div`
    padding: 20px;
`;
let 제목 = styled.h4`
font-size: 25px;`;

function Detail(props){

    let [alert, alert변경] = useState(true);
    let [누른탭, 누른탭변경] = useState(0);
    let [스위치, 스위치변경] = useState(false);
    let history = useHistory();
    let {id} = useParams();
    let 찾은상품 = props.shoes.find((상품)=> 상품.id == id );

    useEffect(()=>{
        let 타이머 = setTimeout(()=>{ alert변경(false) }, 2000);
        return ()=>{clearTimeout(타이머);};
    }, [alert] );

    useEffect(()=>{
        let 저장값 = JSON.parse(localStorage.getItem("최근본상품"));

        if (저장값 === null) {
            저장값 = []

        } else {  
            let 비교 = 저장값.findIndex((a) => a === id);
            if( 비교 >= 0 ) {저장값.splice(비교, 1)} 
        }

        저장값.unshift(id);
        localStorage.setItem("최근본상품", JSON.stringify(저장값));
    }, [] );

    
    return(
        <div className="container">
            <박스> 
                <제목>Detail</제목>
            </박스>

            {
                alert === true
                ? <div className="my-alert--yellow">
                    <p>재고가 얼마 남지 않았습니다.</p>
                </div>
                : null
            }
            
            <div className="row">
                <div className="col-md-6">
                    <img src={`https://codingapple1.github.io/shop/shoes${찾은상품.id + 1}.jpg`} width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{찾은상품.title}</h4>
                    <p>{찾은상품.content}</p>
                    <p>{찾은상품.price}</p>

                    <Info 재고={props.재고} 찾은상품={찾은상품} />

                    <button className="btn btn-danger" onClick={()=>{
                        var new재고 = [...props.재고];
                        new재고[찾은상품.id]--;
                        props.재고변경(new재고);

                        props.dispatch({type: "항목추가", payload: {id: 찾은상품.id, name: 찾은상품.title, quan: 1}});
                        history.push("/cart");
                    }}>주문하기</button> 
                    &nbsp;
                    <button className="btn btn-danger" onClick={()=>{
                        history.goBack();
                    }}>뒤로가기</button> 
                </div>
            </div>

            <Nav className="mt-5" variant="tabs" defaultActiveKey="link-0">
             <Nav.Item>
                <Nav.Link eventKey="link-0" onClick={()=>{누른탭변경(0); 스위치변경(false)}}>상품정보</Nav.Link>
             </Nav.Item>
             <Nav.Item>
                <Nav.Link eventKey="link-1" onClick={()=>{누른탭변경(1); 스위치변경(false)}}>배송정보</Nav.Link>
             </Nav.Item>
             <Nav.Item>
                <Nav.Link eventKey="link-2" onClick={()=>{누른탭변경(2); 스위치변경(false)}}>환불약관</Nav.Link>
             </Nav.Item>
            </Nav>

            <CSSTransition in={스위치} classNames="wow" timeout={500}>
             <TabContent 누른탭={누른탭} 스위치변경={스위치변경}/>
            </CSSTransition>


        </div> 
    )
}

function TabContent(props) {
    useEffect(()=>{
        props.스위치변경(true);
    });

    return (
        <div>
            {
              {
                0 : <p>0000</p>,
                1 : <p>1111</p>,
                2 : <p>2222</p>
              }[props.누른탭]
            }
        </div>
    )
    // 이렇게 if문 쓰는 대신에 위처럼 사용 가능
    // if (props.누른탭 === 0) {
    //     return <div>0000</div>
    // } else if (props.누른탭 === 1) {
    //     return <div>1111</div>
    // } else if (props.누른탭 === 2) {
    //     return <div>2222</div>
    // }
}


function Info(props) {
    return (
        <p>재고 : {props.재고[props.찾은상품.id]}</p>
    )
}


function state를props화(store값) {
    return {
        state: store값.reducer,
        off: store값.reducer2
    }
  }
  
  export default connect(state를props화)(Detail);